import { useState, useEffect, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from './Api/axios';
import { PieChart, Pie, Sector, Cell, Legend, Tooltip, ResponsiveContainer } from 'recharts';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faShapes } from '@fortawesome/free-solid-svg-icons';
import DatePicker, { DateObject } from 'react-multi-date-picker';
import persian from "react-date-object/calendars/persian";
import persian_fa from "react-date-object/locales/persian_fa";
import useAuth from './Hooks/useAuth';
import { useForm, Controller } from "react-hook-form";


const MyPieChart = () => {

    const navigate = useNavigate();
    const { control, handleSubmit } = useForm();
    const [catCl, setCatCl] = useState('telegram_channel');
    const [pieData, setPieData] = useState([]);
    const { setTaging } = useAuth();
    const toDate = new DateObject({ calendar: persian, locale: persian_fa })
    const [fullDate, setFullDate] = useState(`${toDate.year}-${toDate.month.number > 9 ? toDate.month.number : `0${toDate.month.number}`}-${toDate.day > 9 ? toDate.day : `0${toDate.day}`}`)
    const token = localStorage.getItem('access');

    const COLORS = ['#327F51', '#EEB64B', '#9FB4C7', '#A92F5F', '#E54264', '#283D5E', '#C3C48A'];

    const RADIAN = Math.PI / 180;
    const renderCustomizedLabel = ({ cx, cy, midAngle, innerRadius, outerRadius, percent, index }) => {
        const radius = innerRadius + (outerRadius - innerRadius) * 0.5;
        const x = cx + radius * Math.cos(-midAngle * RADIAN);
        const y = cy + radius * Math.sin(-midAngle * RADIAN);

        return (
            <text x={x} y={y} fill="white" textAnchor={x > cx ? 'start' : 'end'} dominantBaseline="central">
                {`${(percent * 100).toFixed(0)}%`}
            </text>
        );
    };

    function turnToTelegramChannel() {
        setCatCl('telegram_channel');
    }

    function turnToTelegramGroups() {
        setCatCl('telegram_groups');
    }

    function turnToInsta() {
        setCatCl('instagram');
    }

    function turnToTweeter() {
        setCatCl('twitter');
    }

    function turnToPost() {
        setCatCl('news_agency');
    }

    const actFilter = {
        backgroundColor: "rgb(1, 31, 75)",
        color: "white",
        borderLeftColor: "rgb(1, 31, 75)",
    };
    const dActFilter = {
    }

    const onSubmit = ({ date }) => {
        setFullDate(`${date?.year}-${date?.month.number > 9 ? date?.month.number : `0${date?.month.number}`}-${date?.day > 9 ? date?.day : `0${date?.day}`}`);
    };

    useEffect(() => {
        let isMounted = true;
        const controller = new AbortController();

        const getPie = async () => {
            try {
                const response = await axios.get(`/${catCl}/sunburst_chart_data?charts_time_filter=${fullDate}`,
                    {
                        headers: { 'Authorization': `Bearer ${token}`, 'Content-Type': 'application/json' }
                    },
                    {
                        signal: controller.signal
                    });
                isMounted && setPieData(response?.data);
                console.log(pieData);
            } catch (err) {
                console.error(err);
            }
        }

        getPie();

        return () => {
            isMounted = false;
            isMounted && controller.abort();
        }
    }, [catCl, fullDate])

    function navi() {
        navigate('/searchnews');
    }

    console.log(pieData);

    // const handleDownloadReports = () =>{
    //        const url = window.URL.createObjectURL(new Blob([pieData])) 
    //        const link = document.createElement('a')
    //        link.href = url
    //        const fileName = `abbas.csv`;
    //        link.setAttribute('download', fileName)
    //        document.body.appendChild(link)
    //        link.click()
    //        link.remove()
    //    }
    const [typeShow, setTypeShow] = useState()

    return (
        <>
            <h5 className='mt-8 px-4'><span className='inline-block place-content-center align-top text-middle-blue text-xl ml-2'><FontAwesomeIcon icon={faShapes} /></span>عملیات روانی</h5>
            <div className='psyc-container min-h-fit bg-white border-slate-400 rounded-md p-4 mt-4'>
                <div className='grid'>
                    <nav className='rounded-lg max-w-fit h-10 z-50 mt-2 md:mb-10'>
                        <ul className='flex flex-col md:flex-row p-0 m-0'>
                            <li className=' text-white grid place-content-center text-base py-2 px-1 w-28 bg-dark-blue md:rounded-r-md select-none' onClick={() => { setTypeShow(!typeShow) }}>انتخاب بستر</li>
                            <li className={`flex relative justify-center items-center text-center text-sm select-none transition-all duration-200 ease-in text-gray-100 bg-middle-blue w-28 py-2 px-1 md:mr-1 hover:bg-dark-blue ${typeShow ? "mobVis" : "mobHid"}`} style={catCl === "telegram_channel" ? actFilter : dActFilter} onClick={turnToTelegramChannel}>کانال‌های تلگرام</li>
                            <li className={`flex relative justify-center items-center text-center text-sm select-none transition-all duration-200 ease-in text-gray-100 bg-middle-blue w-28 py-2 px-1 md:mr-1 hover:bg-dark-blue ${typeShow ? "mobVis" : "mobHid"}`} style={catCl === "telegram_groups" ? actFilter : dActFilter} onClick={turnToTelegramGroups}>گروه‌های تلگرام</li>
                            <li className={`flex relative justify-center items-center text-center text-sm select-none transition-all duration-200 ease-in text-gray-100 bg-middle-blue w-28 py-2 px-1 md:mr-1 hover:bg-dark-blue ${typeShow ? "mobVis" : "mobHid"}`} style={catCl === "twitter" ? actFilter : dActFilter} onClick={turnToTweeter}>توییتر</li>
                            <li className={`flex relative justify-center items-center text-center text-sm select-none transition-all duration-200 ease-in text-gray-100 bg-middle-blue w-28 py-2 px-1 md:mr-1 hover:bg-dark-blue ${typeShow ? "mobVis" : "mobHid"}`} style={catCl === "instagram" ? actFilter : dActFilter} onClick={turnToInsta}>اینستاگرام</li>
                            <li className={`flex relative justify-center items-center text-center text-sm select-none transition-all duration-200 ease-in text-gray-100 bg-middle-blue w-28 py-2 px-1 md:mr-1 hover:bg-dark-blue ${typeShow ? "mobVis" : "mobHid"} md:rounded-l-md`} style={catCl === "news_agency" ? actFilter : dActFilter} onClick={turnToPost}>خبرگذاری‌ها</li>
                        </ul>
                    </nav>
                    {/* <button onClick={handleDownloadReports}>دانلود گزارش</button> */}
                    {/* <div className='flex justify-center'>
                        <DatePicker
                            placeholder='لطفا تاریخ را انتخاب بفرمایید'
                            value={value}
                            onChange={handleChange}
                            calendar={persian}
                            locale={persian_fa}
                            calendarPosition="bottom-right"
                        />
                        {value?.toDate?.().toString()}
                        <button className=' bg-dark-blue text-white hover:bg-middle-blue px-4 mr-2 rounded-md select-none transition-all duration-200 ease-in' onClick={() => { setMyDate(); setCount(count + 1); console.log(value) }}>فیلتر</button>
                    </div> */}
                    <div className='flex justify-center'>
                        <form onSubmit={handleSubmit(onSubmit)} className=''>
                            <Controller
                                control={control}
                                name="date"
                                rules={{ required: true }} //optional
                                render={({
                                    field: { onChange, name, value },
                                    formState: { errors },
                                }) => (
                                    <>
                                        <DatePicker
                                            placeholder="تاریخ مورد نظر را انتخاب کنید"
                                            value={value || "تاریخ مورد نظر را انتخاب کنید"}
                                            onChange={(date) => {
                                                onChange(date?.isValid ? date : "");
                                            }}
                                            format={"YYYY/MM/DD"}
                                            calendar={persian}
                                            locale={persian_fa}
                                            calendarPosition="bottom-right"
                                        />
                                        {errors && errors[name] && errors[name].type === "required" && (
                                            <span>خطایی دریافت شد</span>
                                        )}
                                    </>
                                )}
                            />
                            <input type="submit" value='فیلتر' className='bg-dark-blue text-white hover:bg-middle-blue px-4 py-1 mr-2 rounded-md select-none transition-all duration-200 ease-in' />
                        </form>
                    </div>
                    {/* <p>تاریخ ارسال شده:  {{data}?.format?.("D MMMM YYYY")}</p> */}
                    <ResponsiveContainer width="100%" aspect={3}>
                        <PieChart width={500} height={500}>
                            <Pie
                                data={pieData}
                                cx="50%"
                                cy="50%"
                                innerRadius={65}
                                outerRadius={125}
                                fill="#8884d8"
                                paddingAngle={6}
                                dataKey="value"
                                legendType="circle"
                            >
                                {pieData?.map((entry, index) => (
                                    pieData?.length && <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} onClick={() => { setTaging(entry.name); navi() }} />
                                ))}
                            </Pie>
                            <Tooltip coordinate={{ x: 0, y: 0 }} />
                            <Legend align='center' width={600} wrapperStyle={{ transform: "translate(-50%, -50%)", left: "50%", bottom: "-10%" }}></Legend>
                        </PieChart>
                    </ResponsiveContainer>
                </div>
            </div></>
    )
}

export default MyPieChart